@import "mixins";



body {
	// background: #ffee02
	margin: 0px auto 0;

	background: url('https://westwebdev.github.io/newage/img/body-bg.png') no-repeat 50% 50%;
	background-size: cover;
	background-attachment: fixed; }
section {
	clear: both;
	// padding: 0 30px
 }	// border: 1px solid blue
// 	margin: auto



.max-w {
	max-width: 1100px;
	margin: auto;
	padding-left: 15px !important;
	padding-right: 15px !important; }
.dib {
	display: inline-block;
	margin: auto; }
.h {
	padding: 45px	0 30px;
	text-align: center;
	position: relative;
	margin: 0;
	font-size: 40px;
	font-weight: bold;
	letter-spacing: 1px;
	color: lighten($black, 20%); }





.gallery-h:before {
	content: ' ';
	width: 30%;
	height: 60%;
	// height: 100%
	position: absolute;
	top: 58%;
	// left: 70%
	left: 50%;
	text-align: center;
	background: $white;
	opacity: 0.8;
	@include translate(-50%, -50%);
	border-radius: 50%; }
.h-capt {
	position: relative; }

